.logo {
  padding-top: 1rem;
  z-index: 10;
  width: -moz-fit-content;
  margin-left: 1rem;
  width: fit-content;
}

#logoSpin {
  position: relative;
  background-color: black;
  height: 125px;
  width: 175px;
  padding: 0;
  margin: 0 0 0 2%;
}
#logoSpin img {
  position: absolute;
  height: 100%;
  width: 100%;
}

#cog {
  animation: rotate 30s infinite linear;
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}